<template>
  <div class="grow-table">
    <div class="grow-table__header">发育量表模版列表</div>
    <el-table class="grow-table__list" :data="tableDate">
      <el-table-column
        align="center"
        label="测试月龄"
        min-width="100"
        prop="monthAge"
        fixed
      />
      <el-table-column
        align="center"
        label="模版名称"
        min-width="100"
        prop="name"
      />
      <el-table-column
        align="center"
        label="创建人"
        min-width="100"
        prop="creator"
      />
      <el-table-column
        align="center"
        label="创建时间"
        min-width="110"
        prop="createTime"
      />
      <el-table-column
        align="center"
        label="状态"
        min-width="100"
        prop="status"
      />
      <el-table-column align="center" label="操作" fixed="right" width="220">
        <template slot-scope="scope">
          <div class="fd-team-manage__body__list-handle">
            <el-button
              type="text"
              style="margin-left:10px;"
              :class="scope.row.status === '已发布' ? 'online-style' : ''"
              @click="changeOnlineStatusFun(scope.row)"
              >单击{{
                scope.row.status === '已发布' ? '下线' : '发布'
              }}</el-button
            >
            <el-button
              type="text"
              style="margin-left:10px;"
              @click="handleLook(scope)"
              >查看</el-button
            >
            <el-button
              v-if="scope.row.status !== '已发布'"
              type="text"
              @click="handleEdit(scope)"
              >编辑</el-button
            >
            <el-button
              v-if="scope.row.status !== '已发布'"
              type="text"
              @click="handleDelete(scope)"
              >删除</el-button
            >
          </div>
        </template>
      </el-table-column>
    </el-table>
    <div v-if="page.total > 0" class="grow-table-list__pagination">
      <el-pagination
        :current-page="page.currentPage"
        :page-sizes="pageSizes"
        :page-size="page.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="onlineDialogVisible"
      :before-close="cancelOnlineChangeFun"
      width="400px"
      center
      top="30vh"
    >
      <span class="dialog_content">{{ dialogContent }}</span>
      <span slot="footer" class="dialog-footer">
        <!-- <el-button @click="cancelOnlineChangeFun">取 消 </el-button> -->
        <el-button
          type="primary"
          size="small"
          @click="confirmChangeOnlineStatus"
          >{{ dialogConfirmBtnTxt }}</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getGrowList, getGrowStatus, deleteGrow } from '@/api/grow-table'
import { templateStatus } from '@/filters'
export default {
  props: {
    searchParams: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      page: {
        total: 0,
        pageSize: 10,
        currentPage: 1
      },
      pageSizes: [10, 20, 50],
      addLoading: false,
      addMembers: [],
      members: [],
      loading: false,
      tableDate: [],
      delFlag: false,
      dialogTitle: '发布提示',
      dialogConfirmBtnTxt: '确认发布',
      dialogContent: '',
      onlineDialogVisible: false
    }
  },

  watch: {
    activeName() {
      window.localStorage.setItem('activeName', this.activeName)
      this.addMembers = []
    }
  },
  created() {
    this.handleSearch()
  },
  methods: {
    templateStatus,
    changeOnlineStatusFun(row) {
      this.onlineDialogVisible = true
      this.activeId = row._id
      if (row.status === '已保存' || row.status === '已下线') {
        // 发布
        this.is_on_server = true
        this.dialogTitle = '发布提示'
        this.dialogContent =
          '操作成功后，该发育量表将进入发育量表的列表中，健康管理师将可以发送该发育量表给用户，请确认后操作！'
        this.dialogConfirmBtnTxt = '确认发布'
      } else {
        this.is_on_server = false
        this.dialogTitle = '下线提示'
        // 下先后， 张睿医生团队将不能签约用户，确认下线吗？
        this.dialogContent =
          '操作成功后，该发育量表将从发育量表的列表中移除，健康管理师将无法发送该发育量表给用户，请确认后操作！'
        this.dialogConfirmBtnTxt = '确认下线'
      }
    },
    handleDelete({ row, $index }) {
      this.onlineDialogVisible = true
      this.activeId = row._id
      this.dialogTitle = '删除提示'
      this.dialogContent = `操作成功后，该发育量表将从发育量表模板列表删除，请确认后操作！`
      this.dialogConfirmBtnTxt = '确认删除'
      this.delFlag = true
      // this.tableDate.splice($index, 1)
    },

    async handleCurrentChange(page) {
      this.page.currentPage = page
      await this.handleSearch()
    },

    async handleSizeChange(pageSize) {
      this.page.pageSize = pageSize
      await this.handleSearch()
    },
    cancelOnlineChangeFun() {
      this.onlineDialogVisible = false
      this.handleSearch()
    },
    async confirmChangeOnlineStatus() {
      if (
        this.dialogConfirmBtnTxt === '确认下线' ||
        this.dialogConfirmBtnTxt === '确认发布'
      ) {
        const { data, code } = await getGrowStatus(this.activeId)
        if (code === 20000) {
          if (data.result) {
            this.$message({
              message:
                this.dialogConfirmBtnTxt === '确认下线' ? '已下线' : '已发布',
              type: 'success'
            })
            this.handleSearch()
            this.onlineDialogVisible = false
          } else this.$message({ message: data.message, type: 'error' })
        }
      }
      if (this.dialogConfirmBtnTxt === '确认删除') {
        const { data, code } = await deleteGrow({ id: this.activeId })
        if (code === 20000) {
          if (data.result) {
            this.$message.success('删除成功')
            this.handleSearch()
            this.onlineDialogVisible = false
          } else this.$message.error(data.message)
        }
      }
    },
    async handleSearch(search) {
      if (search) this.page.currentPage = 1
      const params = {
        pageSize: this.page.pageSize,
        page: this.page.currentPage
      }
      if (this.searchParams && this.searchParams.name)
        params.name = this.searchParams.name
      if (search && search.name) params.name = search.name
      const { data, code } = await getGrowList(params)
      if (code === 20000) {
        if (!data.list.length) {
          this.tableDate = []
          return
        }
        this.setPage({ ...data })
        this.setTableData({ ...data })
      }
    },
    setTableData({ list }) {
      this.tableDate = list.map(item => ({
        monthAge: item.month_age ? item.month_age : 0,
        _id: item._id,
        name: item.name ? item.name : '-',
        creator: item.creator ? item.creator.name : '无',
        createTime: this.formatTime(item.created_at),
        status:
          item.status === 'release'
            ? '已发布'
            : item.status === 'save'
            ? '已保存'
            : '已下线'
      }))
    },
    setPage({ pageSize, count }) {
      this.page.pageSize = pageSize
      this.page.total = count
    },
    handleLook({ row }) {
      this.$router.push('/system-settings/grow-template/look/' + row._id)
    },
    handleEdit({ row }) {
      this.$router.push('/system-settings/grow-template/edit/' + row._id)
    }
  }
}
</script>

<style lang="scss">
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';

.grow-table {
  padding: 10px 20px;
  &__header {
    padding: 0 20px;
    width: 100%;
    height: 50px;
    line-height: 50px;
    font-size: 14px;
    font-family: PingFangSC-Medium;
    font-weight: 600;
    color: #17233d;
    background: #f5f7fa;
    letter-spacing: 0;
  }
  &__list {
    .el-table__header th {
      font-weight: 600;
      color: #17233d;
      letter-spacing: 0;
    }
  }
  .dialog_content {
    line-height: 18px;
    font-size: 14px;
    font-family: PingFangSC-Regular;
    text-align: center;
    color: #17233d;
  }
  .grow-table-list__pagination {
    margin-top: 20px;
    text-align: center;
  }
}
</style>
