<template>
  <div>
    <el-form
      ref="growTableFormInfo"
      label-position="left"
      :model="templateFormData"
      class="search__form"
    >
      <el-row :gutter="24">
        <el-col :span="6">
          <el-form-item label="模版名称" prop="name" label-width="80px">
            <el-input
              v-model="templateFormData.name"
              placeholder="请输入模版名称"
              size="small"
              clearable
            />
          </el-form-item>
        </el-col>
        <el-col :span="18" style="margin-top:6px;">
          <el-button type="primary" size="mini" @click="handleSearch"
            >搜索</el-button
          >
          <el-button
            type="primary"
            style="float:right"
            plain
            size="mini"
            @click="createdTemplate"
            >新建模版</el-button
          >
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
export default {
  name: 'HeaderForm',
  data() {
    return {
      growTableHeaderForm: {
        disabled: false
      },
      templateFormData: {
        name: ''
      }
    }
  },
  mounted() {},
  methods: {
    handleSearch() {
      this.$refs['growTableFormInfo'].validate(valid => {
        if (valid) {
          this.$emit('search', this.templateFormData)
        } else {
          return false
        }
      })
    },
    createdTemplate() {
      this.$router.push({ path: '/system-settings/grow-template/add' })
    }
  }
}
</script>
<style lang="scss" scoped>
.search__form {
  border-bottom: 1px dotted #dddedf;
  // padding-bottom: -20px;
  margin-bottom: 20px;
  padding: 20px 20px 0;
}
</style>
