<template>
  <div>
    <HeaderForm @search="setSearch" />
    <GrowTable ref="growTable" :search-params="searchParams" />
  </div>
</template>
<script>
import HeaderForm from './components/HeaderForm.vue'
import GrowTable from './components/GrowTable.vue'

export default {
  components: {
    HeaderForm,
    GrowTable
    // GrowTab
  },
  data() {
    return {
      searchParams: {}
    }
  },
  methods: {
    setSearch(val) {
      this.searchParams = val
      this.$refs.growTable.handleSearch(val)
    }
  }
}
</script>
